import React, { useEffect, useState } from 'react';
import './App.css';
import Dashboard from './Dashboard';
import Header from './Header';
import Review from './Review';
import Reviews from './Reviews';
import AboutMe from './about-me/AboutMe';
import AboutUs from './about-us/AboutUs';

import { Route, Switch } from 'react-router-dom';

function App() {

  const [reviews, setReviews] = useState([]);
  // —ç—Ç–æ—Ç —Ö—É–∫ –∏—Å–ø–æ–ª—å–∑—É–µ—Ç—Å—è –µ–¥–∏–Ω–æ–∂–¥—ã, –ø—Ä–∏ –∑–∞–ø—É—Å–∫–µ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞
  useEffect(() => {
    // –ø–æ–ª—É—á–∞–µ–º –¥–∞–Ω–Ω—ã–µ —Å —Å–µ—Ä–≤–µ—Ä–∞
    fetch('https://api.nomoreparties.co/emoji-critic-rus').then((res) => {
      return res.json();
    }).then((parsedReviews) => {
      // —Ñ–æ—Ä–º–∞—Ç–∏—Ä—É–µ–º –¥–∞–Ω–Ω—ã–µ –∏, –∏—Å–ø–æ–ª—å–∑—É—è setData, –æ–±–Ω–æ–≤–ª—è–µ–º —Ç–µ–∫—É—â–∏–π —Å—Ç–µ–π—Ç
      const reviews = Object.values(parsedReviews);
      setReviews(reviews);
    })
  }, []);

  return (
    <div className="App">
      <Header />
      <Switch>
        <Route exact path="/">
          <Dashboard />
        </Route>
        <Route exact path="/reviews">
          <Reviews reviews={reviews}/>
        </Route>
        <Route exact path="/reviews/:id">
          <Review reviews={reviews}/>
        </Route>
        <Route path="/about-me">
          <AboutMe />
        </Route>
        <Route path="/about-us">
          <AboutUs />
        </Route>
      </Switch>
    </div>
  );
}

export default App;
